.container{
    display:flex;
    flex-direction: column;
    margin:auto;
    width: 600px;
    margin-top: 200px;
    background:rgb(253, 254, 253);
    padding-bottom: 30px;
}
.header{
    display:flex;
    flex-direction:column;
    align-items: center;
    gap: 9px;
    width: 100%;
    margin-top: 30px;
}
.text{
    color: rgb(178, 131, 60);
    font-size: 48px;
    font-weight: 700;
}
.underline{
    width: 61px;
    height: 6px;
    background: yellowgreen;
    border-radius: 9px;
}
.inputs{
    margin-top: 55px;
    display: flex;
    flex-direction: column;
    gap: 25px;
}
.input{
    display: flex;
    align-items: center;
    margin: auto;
    width: 480px;
    height:80px;
    background: whitesmoke;
}
.input input{
    height:50px;
    width:400px;
    background:transparent;
    border: none;
    outline: none;
    color: #797979;
    font-size: 19px;

}
.input img{
    margin: 0px 30px;
}
.forgot-password{
    padding-left: 62px;
    margin-top: 27px;
    color: #797979;
    font-size: 18px;
}
.forgot-password span{
    color: aquamarine;
    cursor: pointer;
}
.submit-container{
    display: flex;
    gap: 30px;
    margin:60px auto;

}
.submit{
    display:flex;
    justify-content: center;
    align-items: center;
    width: 220px;
    height:59px;
    color: aliceblue;
    background: burlywood;
    border-radius: 50px;
    font-size: 19px;
    font-weight: 700;
    cursor: pointer;
}
.gray{
    background: whitesmoke;
    color: #797979;
}